
import { useState } from 'react'
import "@lottiefiles/lottie-player"
import Sidebar from './Sidebar'

import { HiMenuAlt4 } from 'react-icons/hi'

const Nav = () => {
    const [showNav, setShowNav] = useState(true) 

    return (
        <div className='nav'>
         <HiMenuAlt4 className='giHam' onClick={() => {setShowNav(!showNav)}} />
         <Sidebar show={showNav} />
             <h1 className ='recipe-h1'>Recipe book</h1>
             <script src="https://unpkg.com/@lottiefiles/lottie-player@latest/dist/lottie-player.js"></script>
             <lottie-player src="https://assets2.lottiefiles.com/packages/lf20_UGvCSC/loading_animation.json"  background="transparent"  speed="1"  style={{width: '140px', height: '150px',marginTop:'-15px',marginLeft:'-90px'}}  loop  autoplay></lottie-player>

        </div>
    )
}

export default Nav
