import React from 'react'
import Img from '../images/plate.jpeg' 

const Home = () => {
    return (
        <div className='home'>
            <h1>The best place for recipes</h1>
            <script src="https://unpkg.com/@lottiefiles/lottie-player@latest/dist/lottie-player.js"></script>
            <img className='home-img' src={Img}  alt='image'/>
            <span><lottie-player className='chef' src="https://assets2.lottiefiles.com/datafiles/7U4SpIu4WhtVlaZ/data.json"  background="transparent"  speed="1"  style={{width: '150px', height:'250px',position:'absolute',top:'36%',left:'63%'}}  loop  autoplay></lottie-player></span>
        </div>
    )
}

export default Home
